html {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    padding: 0;
    height: 100%;
}

#root {
    height: 100%;
}

.table {
    table-layout: fixed;
    width: 100%;
    margin: 0;
    border-spacing: 0;
    border-collapse: collapse;
    position: relative;
}

.table>thead {
    position: sticky;
    top: 0;
    background: #b4baff;
}

.table>thead>tr>td {
    margin: 0;
    padding: 4px 7px;
    font-family: 'Courier New', Courier, monospace;
    color: #000;
    vertical-align: top;
    cursor: default;
    word-break: break-all;
    border: 1px solid #000;
}

.table>tbody>tr:hover {
    background: #b4baff;
}

.table>tbody>tr:nth-child(even)>td {
    background: #e2f5ff;
}

.table>tbody>tr>td {
    margin: 0;
    padding: 4px 7px;
    font-family: 'Courier New', Courier, monospace;
    color: #000;
    vertical-align: top;
    cursor: default;
    word-break: break-all;
    border: 1px solid #000;
}