.DataBlock {
    padding: 0 0 0;
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    justify-content: space-between;
    position: relative;
}

.DataBlock__buttons-wrap {
    display: block;
    position: relative;
}

.DataBlock__buttons {
    display: flex;
    flex-flow: column nowrap;
    gap: 2px;
    visibility: hidden;
}

.DataBlock--isMouseOver .DataBlock__buttons {
    visibility: visible;
}

.DataBlock__button {
    cursor: pointer;
    white-space: nowrap;
    font-family: 'Courier New', Courier, monospace;
    color: black;
    font-weight: bold;
    font-size: 12px;
}

.DataBlock__copyed {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #c5d6ffad;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
}