.ThSort {
    user-select: none;
    cursor: pointer;
}

.ThSort--active {
    font-weight: bold;
    color: black;
    text-shadow: 0 0 0 #000;
}

.ThSort__arr {
    display: inline-block;
    vertical-align: middle;
    font-weight: bold;
    font-size: 36px;
    line-height: 10px;
    height: 17px;
}